@import './variebles.scss';

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-size: $defaultFontSize;
  font-family: $defaultFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

li,
ol,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
.subtitle {
  display: block;
  margin: 0 auto;
  text-align: center;
  color: $brandColor;
}
button {
  height: 40px;
  padding: 0 10px;
  font-size: 17px;
  font-weight: 600;
  text-align: center;
  border-radius: 7px;
  border: none;

  color: $activeColor;
  background-color: $brandBgColor;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    cursor: pointer;
    opacity: 0.7;
  }
}

